import utils from "../../../utils.js";
export default {
	data() {
		return {
			code: '',
			active: 0,
			scanMsg: '',
			scanType: '',
			fromm: '安全巡查',
			placeName: '请扫描二维码',
			lastman: '',
			lasttime: '',
			wait:0
		}
	},
	methods: {
		judgeIscheck(id,type) {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/school/Get_IsCheck',
				data: {
					QRCode: id,
					type: type,
					isCheck: "true"
				}
			}).then(data => {
				var [error,res] = data;
				console.log(res)
				if (res.data.code == 0) {
					self.getPlace(id,type)
					self.getPlaceNum(type);
					if (self.scanType == 'XF') {
						self.active = 3;
						// self.header = '消防检查'
					} else if(self.scanType == 'XG'){
						self.active = 6;
						// self.header = '巡更检查'
					} else {
						self.active = 7;
						// self.header = '巡更检查'
					}
				} else if  (res.data.code == 1) {
					self.showToast('网络错误');
					self.scanMsg = '';
					self.scanType = '';
				} else {
					self.showToast(res.data.msg);
					self.scanMsg = '';
					self.scanType = '';
				}
			})
		},
		getDetail() {
			console.log(this.scanType);
			if (this.scanType == 'XF') {
				uni.navigateTo({
					url: "../../check/fireControlCheck/fireControlCheck"
				})
			} else if (this.scanType == 'XG') {
				uni.navigateTo({
					url: '../../check/schoolCheck/schoolCheck'
				})
			}
		},
		showToast(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		getPlace(id,type) {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/School/Get_PlaceWay',
				data: {
					QRCode: id,
					type: type
				}
			}).then(data => {
				var [error,res] = data;
				console.log(res)
				if (res.data.code ==0) {
					self.placeName = res.data.data;
				}
			})
		},
		getPlaceNum(type) {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Web/Get_XGXFPointTotal'
				
			}).then(data => {
				var [error,res] = data;
				console.log(res)
				if (res.data.code ==0) {
					var foo = res.data.data;
					for (let s of foo) {
						if (s.type == type) {
							self.wait = s.count;
						}
					}
					
				}
			})
		},
		getData(id,type) {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/School/Get_HD',
				data: {
					QRCode: id,
					type: type,
					RYXH: self.$store.state.userInfo.RYXH,
					isCheck: 'true'
				},
			}).then(data => {
				// console.log(data)
				var [error, res] = data;
				
				console.log(res.data);
				self.lastman = res.data.info.RYMC || '未检查';
				self.lasttime = res.data.info.checkTime || '未检查';
			})
		},
		getCode() {
			var self = this;
			self.active = 0;
			uni.showLoading({
				title:'扫描中。。。',
				mask:true
			})
			uni.scanCode({
				onlyFromCamera: true,
				success: function(res) {
					console.log(res)
					if (utils.GetUrlParam('id',res.result)) {
						let id = utils.GetUrlParam('id',res.result);
						let type = utils.GetUrlParam('type',res.result) || 'XF';
						self.getData(id,type);
						// let type = 'XG';
						self.scanMsg = id;
						self.scanType = type;
						
						self.judgeIscheck(id,type);
						console.log(id,type)
						
						uni.hideLoading();
					} else {
						uni.hideLoading();
						self.showModel('扫描失败')
					}
				},
				fail() {
					uni.hideLoading();
					self.showToast('扫描失败！')
				}
			})
		}
	}
}
